.button {
    border: none;
    color: white;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    cursor: pointer;
    border-radius: 8px;
  }
  
  .greenbutton {background-color: #4CAF50;} /* Green */
  .redbutton {background-color: #ec22ba;} /* red */